const isPair = (arr) => arr.every(Boolean);
const getSignedPair = (pair) => {
    let wipPair = [pair.key, pair.salt];
    if (!isPair(wipPair)) {
        wipPair = [KEY, SALT];
        if (!isPair(wipPair))
            return undefined;
    }
    return {
        salt: wipPair[1].split(",")[0].trim(),
        key: wipPair[0].split(",")[0].trim(),
    };
};
export default getSignedPair;
